import { Component } from 'react'
import { observer, inject } from 'mobx-react'
import {
  View,
  Button,
  Text,
  Image
} from '@tarojs/components'
import './style.scss'

@inject('store')
@observer
class Index extends Component {

  componentDidShow() {
    let { actStore } = this.props.store
    // 触发mobx中的action
    console.log('show')
    actStore.getActList()
  }

  renderList() {
    let { actStore } = this.props.store
    return actStore.list.map((ele,idx)=>(
      <View className="row" key={idx}>
        <Image src={'http://localhost:8888'+ele.banner} />
        <View>
          <View className='title'>{ele.title}</View>
          <View className='desc'>
            <Text>第 {ele.no} 期</Text>
            <Text>{ele.start_time}</Text>
            -
            <Text>{ele.end_time}</Text>
          </View>
          <View className='btn'>查看队伍</View>
        </View>
      </View>
    ))
  }

  render () {
    console.log('this props', this.props)
    return (
      <View className='page-welfare'>
        { this.renderList() }
        <View className='tip'>
          <View>你还可以通过“阅读时长积累”获得无限卡</View>
          <View>1.阅读即送无限卡。阅读时间越长，赠送无限卡越多。每周最多可获得4天无限卡。</View>
        </View>
      </View>
    )
  }
}
export default Index
